import React from "react";
import { Link } from "react-router-dom";
import "./index.scss";
import { Sidebar } from "./Sidebar";
import { MainNavList } from "./NavLists/MainNavList";
export const Nav = (props) => {
  return (
    <nav
      className={
        props.btn
          ? "navbar  fixed-top navbar-expand-lg  bg-white "
          : "navbar  fixed-top navbar-expand-lg  bg-gray"
      }
    >
      <div className="container-fluid">
        <Link className="link navbar-brand" to="/lms">
          <img
            alt="E-business"
            width="70px"
            height="70px"
            src="https://ebusiness.el-nemr.net/storage/settings/March2020/LhSTxzmMs5JHAzvRqBht.png"
          />
        </Link>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ml-auto mb-2 mb-lg-0">
            <div className="responsive">
              <div className=" d-flex">
                <MainNavList />
              </div>
            </div>
            <Sidebar />
          </ul>
        </div>
      </div>
    </nav>
  );
};
