import React, { Fragment } from "react";
import * as AiIcons from "react-icons/ai";
import * as FaIcons from "react-icons/fa";
import { Link } from "react-router-dom";
import "./index.scss";

export const Contact = () => {
  return (
    <Fragment>
      <div className="container">
        <div className="row">
          <div className="col-md-6 ">
            <form>
              <div className="form-group">
                <input className="form-control" id="name" type="text" />
                <label style={{ fontSize: "1.5rem", fontWeight: "800" }}>
                  Name{" "}
                </label>
              </div>
              <div className="form-group">
                <input className="form-control" id="name" type="text" />
                <label style={{ fontSize: "1.5rem", fontWeight: "800" }}>
                  E-mail{" "}
                </label>
              </div>
              <div className="form-group">
                <input className="form-control" id="name" type="text" />
                <label style={{ fontSize: "1.5rem", fontWeight: "800" }}>
                  Company{" "}
                </label>
              </div>
              <div className="form-group">
                <input className="form-control" id="name" type="text" />
                <label style={{ fontSize: "1.5rem", fontWeight: "800" }}>
                  Subject{" "}
                </label>
              </div>
              <div className="form-group">
                <textarea
                  className="form-control"
                  id="name"
                  type="text"
                ></textarea>
                <label style={{ fontSize: "1.5rem", fontWeight: "800" }}>
                  Message{" "}
                </label>
              </div>
              <button
                className="btn px-5 py-2 my-4 "
                style={{
                  fontSize: "1.5rem",
                  borderRadius: "40px",
                  fontWeight: "700",
                  backgroundColor: "#060b26 ",
                  color: "white",
                }}
              >
                Send
              </button>
            </form>
          </div>
          <div className="col-md-6 pl-5 ">
            <div style={{ height: "100%", position: "relative" }}>
              <div style={{ fontWeight: "800" }}>
                <h1 className="pb-5" style={{ fontSize: "3rem" }}>
                  Contact us
                </h1>
                <p
                  className="pt-5"
                  style={{ fontSize: "1.5rem", color: "gray" }}
                >
                  325 B South Academy - New Cairo
                  <br /> Postal code: (11865)
                  <br />
                  contactus@ebschoolonline.com <br />
                  +20 1005663147
                </p>
              </div>
              <div
                className="social-icons  "
                style={{
                  position: "absolute",
                  bottom: "30px",
                  right: "0",
                }}
              >
                <Link to="/">
                  <AiIcons.AiFillFacebook />
                </Link>
                <Link to="/">
                  <FaIcons.FaInstagram />
                </Link>
                <Link to="/">
                  <AiIcons.AiFillLinkedin />
                </Link>
                <Link to="/">
                  <FaIcons.FaYoutube />
                </Link>
                <Link to="/">
                  <FaIcons.FaWhatsapp />
                </Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
