import React, { Fragment, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import * as AiIcons from "react-icons/ai";
import { validateCode, validatePassword } from "../Utils";
import "../login.scss";
import { validateEmail } from "../Utils";
export const ForgotPasswordForm = () => {
  const [toggle, setToggle] = useState(false);
  const [view, setView] = useState(false);
  const [email, setEmail] = useState("");
  const [error, setError] = useState(false);
  const [code, setCode] = useState("");
  const [password, setPassword] = useState("");
  const [rePassword, setRePassword] = useState("");
  const [errors, setErrors] = useState({});
  const [resetAlert, setResetAlert] = useState(false);

  const rePassCheck = () => {
    if (rePassword !== password) {
      return false;
    } else {
      return true;
    }
  };
  let history = useHistory();
  useEffect(() => {
    const handleToggle = () => {
      setToggle(false);
    };
    window.addEventListener("click", handleToggle);
    window.addEventListener("keydown", handleToggle);

    const handleResetAlert = () => {
      setResetAlert(false);
      history.push("/login");
    };

    if (resetAlert) {
      window.addEventListener("click", handleResetAlert);
      window.addEventListener("keydown", handleResetAlert);
    }

    return () => {
      window.removeEventListener("click", handleResetAlert);
      window.removeEventListener("keydown", handleResetAlert);
      window.removeEventListener("click", handleToggle);
      window.removeEventListener("keydown", handleToggle);
      setToggle(false);
    };
  }, [resetAlert, history]);

  return (
    <Fragment>
      <div
        className={toggle ? "" : "d-none"}
        style={{
          background: "rgba(0,0,0,0.5)",
          width: "100%",
          height: "100%",
          position: "fixed",
          top: "0",
          bottom: "0",
          right: "0",
          left: "0",
        }}
      >
        {" "}
        <div
          className="py-3 "
          style={{
            width: "30%",
            margin: "auto",
            marginTop: "20px",
            background: "white",
            position: "relative",
          }}
        >
          <AiIcons.AiOutlineClose
            style={{
              color: "black",
              cursor: "pointer",
              position: "absolute",
              right: "0",
              top: "0",
              fontSize: "1.8rem",
              zIndex: "99999",
            }}
            onClick={() => {
              setToggle(false);
            }}
          />

          <h3 className="pl-3 font-weight-bold">Success</h3>
          <hr />
          <p className="pl-3">Your password has been reset</p>
        </div>
      </div>
      <div
        className={resetAlert ? "" : "d-none"}
        style={{
          background: "rgba(0,0,0,0.5)",
          width: "100%",
          height: "100%",
          position: "fixed",
          top: "0",
          bottom: "0",
          right: "0",
          left: "0",
        }}
      >
        {" "}
        <div
          className="py-3 "
          style={{
            width: "30%",
            margin: "auto",
            marginTop: "20px",
            background: "white",
            position: "relative",
          }}
        >
          <AiIcons.AiOutlineClose
            style={{
              color: "black",
              cursor: "pointer",
              position: "absolute",
              right: "0",
              top: "0",
              fontSize: "1.8rem",
              zIndex: "99999",
            }}
          />

          <h3 className="pl-3 font-weight-bold">Success</h3>
          <hr />
          <p className="pl-3">Your password has been changed</p>
        </div>
      </div>
      <h2 className="pt-5 pb-3 font-weight-bold">
        {view ? "Reset your password" : "Forgot Password"}
      </h2>
      <h6 className="pb-3 font-weight-bold " style={{ color: "gray" }}>
        {view
          ? "Enter the reset code and the new password"
          : "Enter your E-mail"}
      </h6>

      <form
        className={view ? "d-none" : ""}
        onSubmit={(e) => {
          e.preventDefault();

          if (validateEmail(email) === true) {
            setToggle(!toggle);
            setView(true);
          } else {
            setError(true);
          }
        }}
      >
        {" "}
        <div className="mb-4 ">
          <input
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            type="email"
            placeholder="    E-mail"
            className="form-control  py-4"
          />{" "}
          <small className="text-danger font-weight-bold">
            {error && "Not valid Email  "}
          </small>
        </div>
        <div className="text-center py-3">
          {" "}
          <br />{" "}
          <button
            className="text-center w-75 py-2 btn btn-primary"
            style={{
              borderRadius: "50px",
              fontSize: "1.5rem",
              fontWeight: "800",
            }}
          >
            Submit
          </button>
        </div>
      </form>
      <form
        className={view ? "" : "d-none"}
        onSubmit={(e) => {
          e.stopPropagation();
          e.preventDefault();
          setErrors({
            code: !validateCode(code),
            password: !validatePassword(password),
            rePassword: !rePassCheck(rePassword),
          });
          if (
            validateCode(code) === false ||
            validatePassword(password) === false ||
            rePassCheck() === false
          ) {
            return;
          } else {
            setResetAlert(true);
          }
        }}
      >
        {" "}
        <div className="mb-4 ">
          <input
            value={code}
            onChange={(e) => setCode(e.target.value)}
            type="text"
            placeholder="    Reset code"
            className="form-control  py-4"
          />{" "}
          <small
            className={errors.code ? "text-danger font-weight-bold" : undefined}
          >
            {errors.code ? "Not valid code" : "Four-digit verification code"}
          </small>
        </div>
        <div className="mb-4 ">
          <input
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            type="password"
            placeholder="    New password"
            className="form-control  py-4"
          />{" "}
          <small className="text-danger font-weight-bold">
            {errors.password && "Password must be at least 8 characters"}
          </small>
        </div>
        <div className="mb-4 ">
          <input
            value={rePassword}
            onChange={(e) => setRePassword(e.target.value)}
            type="password"
            placeholder="    Re-new password"
            className="form-control  py-4"
          />{" "}
          <small className="text-danger font-weight-bold">
            {errors.rePassword && "Not match"}
          </small>
        </div>
        <div className="text-center pb-3"></div>
        <div className="text-center py-3">
          {" "}
          <br />{" "}
          <button
            className="text-center w-75 py-2 btn btn-primary"
            style={{
              borderRadius: "50px",
              fontSize: "1.5rem",
              fontWeight: "800",
            }}
          >
            Submit
          </button>
        </div>
      </form>
    </Fragment>
  );
};
