import React, { Fragment, useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./index.scss";
import axios from "axios";

export const Testimonials = () => {
  const [students, setStudents] = useState([]);
  let settings = {
    infinite: false,
    speed: 1000,
    arrows: true,
    slidesToShow: 3,
    slidesToScroll: 3,

    responsive: [
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  useEffect(() => {
    axios
      .get("https://ebusiness.el-nemr.net/api/testamonials")
      .then((res) => setStudents(res.data.response.data));
  }, []);

  return (
    <Fragment>
      <div
        className="items d-flex justify-content-center align-items-center "
        style={{ height: "700px" }}
      >
        <div className="item">
          <div className="title pb-5 d-flex justify-content-center align-items-center">
            <h2>TESTIMONIALS</h2>
            <h3>TESTIMONIALS</h3>
          </div>

          <div className="item container  ">
            <Slider {...settings}>
              {students.map((student) => {
                return (
                  <div key={student.id} className="quotes">
                    <div style={{ position: "relative" }}>
                      <img
                        alt={student.image_alt}
                        style={{
                          width: "100px",
                          position: "absolute",
                          bottom: "20px",
                          left: "20px",
                          zIndex: "5",
                          borderRadius: "20px",
                        }}
                        src={student.image_url}
                      />
                      <div className="quote pl-5 ">
                        <blockquote className="quote-card ">
                          <p>{student.student_testamonial}</p>

                          <p className="name">
                            {student.student_name}
                            <br />
                            <span>{student.student_location}</span>
                          </p>
                        </blockquote>
                      </div>
                    </div>
                  </div>
                );
              })}
            </Slider>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
