import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./index.scss";
import { settings } from "./utils";
import axios from "axios";
export const Instructors = () => {
  const [instructors, setInstructors] = useState([]);
  useEffect(() => {
    axios
      .get("https://ebusiness.el-nemr.net/api/instructors")
      .then((res) => setInstructors(res.data.response.data));
  }, []);
  return (
    <div className="bg-light py-5" style={{ height: "700px" }}>
      <div className="container">
        <h1 className="my-5">Our Instructors</h1>
        <Slider {...settings}>
          {" "}
          {instructors.map((instructor) => {
            return (
              <div key={instructor.id} className="instructor-container">
                <div className="instructor">
                  <img
                    alt={instructor.image_alt}
                    src={instructor.image_url}
                    className="img-fluid"
                  />
                  <div className="desc">
                    <h5 className="px-3 my-4">{instructor.instructor_name}</h5>
                    <p>
                      {instructor.instructor_description ||
                        instructor.instructor_title}
                    </p>
                  </div>
                </div>
              </div>
            );
          })}
        </Slider>
      </div>
    </div>
  );
};
