import React, { Fragment, useState, useEffect } from "react";
import * as FaIcons from "react-icons/fa";
import "./index.scss";

export const Search = () => {
  const [toggle, setToggle] = useState(false);
  useEffect(() => {
    window.addEventListener("click", (e) => {
      setToggle(false);
    });

    window.addEventListener("keydown", (e) => {
      if (e.key === "Escape") {
        setToggle(false);
      }
    });
  }, [toggle]);
  return (
    <Fragment>
      <li
        style={{ cursor: "pointer" }}
        className={toggle ? "d-none" : "link nav-link"}
      >
        <FaIcons.FaSearch
          onClick={(e) => {
            setToggle(!toggle);
            e.stopPropagation();
          }}
        />
      </li>
      <form className={toggle ? "" : "d-none"}>
        <input
          onClick={(e) => e.stopPropagation()}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              window.alert("aho");
            }
          }}
          type="text"
          placeholder="Search..."
          className="form-control"
        />
      </form>
    </Fragment>
  );
};
