import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";

export const Course = (props) => {
  let courseId = props.match.params.courseId;
  let categoryId = props.match.params.categoryId;
  const [courses, setCourses] = useState([]);
  useEffect(() => {
    axios
      .get(`https://ebusiness.el-nemr.net/api/course-categories/${categoryId}`)
      .then((res) => setCourses(res.data.response.data));
  }, []);
  useEffect(() => {
    let course = courses.filter((course) => {
      return course.id == courseId;
    });
    console.log(course);
    // course.map((prop) => console.log(prop.course_name));
  }, [courses]);

  return (
    <Fragment>
      <div className="container">
        <div className="row">{}</div>
      </div>{" "}
    </Fragment>
  );
};
