import axios from "axios";
import React, { Fragment, useState, useEffect } from "react";
import * as AiIcons from "react-icons/ai";
import { NavLink, useHistory } from "react-router-dom";
import "../login.scss";
import "../Utils";
export const RegisterNext = (props) => {
  const [courses, setCourses] = useState([]);
  const [courses1, setCourses1] = useState([]);
  const [courses2, setCourses2] = useState([]);
  const [courses3, setCourses3] = useState([]);
  const [courses4, setCourses4] = useState([]);
  const [courses5, setCourses5] = useState([]);
  const [course, setCourse] = useState("");
  const [payment, setPayment] = useState("");
  const [errors, setErrors] = useState({ course: false, payment: false });
  const [toggle, setToggle] = useState(false);

  let history = useHistory();

  useEffect(() => {
    axios
      .get("https://ebusiness.el-nemr.net/api/course-categories/14")
      .then((res) => setCourses(res.data.response.data));
    axios
      .get("https://ebusiness.el-nemr.net/api/course-categories/6")
      .then((res) => setCourses1(res.data.response.data));
    axios
      .get("https://ebusiness.el-nemr.net/api/course-categories/8")
      .then((res) => setCourses2(res.data.response.data));
    axios
      .get("https://ebusiness.el-nemr.net/api/course-categories/10")
      .then((res) => setCourses3(res.data.response.data));
    axios
      .get("https://ebusiness.el-nemr.net/api/course-categories/15")
      .then((res) => setCourses4(res.data.response.data));
    axios
      .get("https://ebusiness.el-nemr.net/api/course-categories/1")
      .then((res) => setCourses5(res.data.response.data));
    const handleEvent = () => {
      setToggle(false);
      history.push("/login");
    };

    if (toggle) {
      window.addEventListener("click", handleEvent);
      window.addEventListener("keydown", handleEvent);
    }

    return () => {
      window.removeEventListener("click", handleEvent);
      window.removeEventListener("keydown", handleEvent);
      setToggle(!toggle);
    };
  }, [toggle, history]);

  return (
    <Fragment>
      <NavLink to="/">
        <img
          alt="E-business"
          style={{
            width: "100px",
            height: "100px",
            background: "white",
          }}
          src="https://ebusiness.el-nemr.net/storage/settings/March2020/LhSTxzmMs5JHAzvRqBht.png"
        />
      </NavLink>
      <NavLink to="/">
        <AiIcons.AiOutlineClose
          style={{
            color: "black",
            cursor: "pointer",
            position: "absolute",
            right: "15px",
            top: "15px",
            fontSize: "3rem",
            zIndex: "99999",
          }}
        />
      </NavLink>
      <div className="py-5 w-75 m-auto">
        <h2 className="pt-5 pb-3 font-weight-bold">Sin Up</h2>
        <form
          onSubmit={(e) => {
            e.preventDefault();
            if (course === "") {
              setErrors({ course: true });
            } else if (payment === "") {
              setErrors({ payment: true });
            } else {
              setToggle(true);
            }
          }}
          className="w-100 m-auto row justify-content-center"
        >
          <div className="col-12 select-form pr-0 pl-0 py-4">
            <select
              value={course}
              onChange={(e) => setCourse(e.target.value)}
              className=" py-3 w-100"
            >
              <option defaultValue>Courses</option>
              {courses.map((course) => {
                return (
                  <option key={course.id} value={course.course_name}>
                    {course.course_name}
                  </option>
                );
              })}{" "}
              {courses1.map((course) => {
                return (
                  <option key={course.id} value={course.course_name}>
                    {course.course_name}
                  </option>
                );
              })}{" "}
              {courses2.map((course) => {
                return (
                  <option key={course.id} value={course.course_name}>
                    {course.course_name}
                  </option>
                );
              })}
              {courses3.map((course) => {
                return (
                  <option key={course.id} value={course.course_name}>
                    {course.course_name}
                  </option>
                );
              })}
              {courses4.map((course) => {
                return (
                  <option key={course.id} value={course.course_name}>
                    {course.course_name}
                  </option>
                );
              })}
              {courses5.map((course) => {
                return (
                  <option key={course.id} value={course.course_name}>
                    {course.course_name}
                  </option>
                );
              })}
            </select>
            <small
              className={
                errors.course ? "text-danger font-weight-bold" : undefined
              }
            >
              {errors.course && "Course field is required"}
            </small>
          </div>
          <div className="col-12 pr-0 pl-0 pb-3 ">
            <select
              value={payment}
              onChange={(e) => {
                setPayment(e.target.value);
              }}
              className=" py-3 w-100"
            >
              <option defaultValue>Select payment method</option>
              <option value="cash">Cash</option>
              <option value="kiosk">Kiosk</option>
              <option value="card"> Card</option>
            </select>{" "}
            <small
              className={
                errors.payment ? "text-danger font-weight-bold" : undefined
              }
            >
              {errors.payment && "Payment field is required"}
            </small>
          </div>

          <div className="text-center pb-3">
            <p style={{ color: "gray", fontWeight: "800" }}>
              By continuing, you agree to E-business school
              <span style={{ color: "black" }}>
                {" "}
                Terms of Service, Privacy Policy
              </span>
            </p>{" "}
            <br />{" "}
            <button
              className="text-center w-75 py-2 btn btn-primary"
              style={{
                borderRadius: "50px",
                fontSize: "1.5rem",
                fontWeight: "800",
              }}
            >
              Next
            </button>
            <p className="pt-4">Already have an account? </p>
            <NavLink
              style={{
                background: "white",
                color: "#09c",
                fontSize: "1.5rem",
                textDecoration: "none",
                fontWeight: "900",
              }}
              to="/login"
            >
              Login
            </NavLink>
          </div>
        </form>
      </div>
      <div
        className={toggle ? "" : "d-none"}
        style={{
          background: "rgba(0,0,0,0.5)",
          width: "100%",
          height: "100%",
          position: "fixed",
          top: "0",
          bottom: "0",
          right: "0",
          left: "0",
        }}
      >
        {" "}
        <div
          className="py-3 "
          style={{
            width: "30%",
            margin: "auto",
            marginTop: "20px",
            background: "white",
            position: "relative",
          }}
        >
          <AiIcons.AiOutlineClose
            style={{
              color: "black",
              cursor: "pointer",
              position: "absolute",
              right: "0",
              top: "0",
              fontSize: "1.8rem",
              zIndex: "99999",
            }}
            onClick={() => {
              setToggle(false);
            }}
          />

          <h3 className="pl-3 font-weight-bold">Success</h3>
          <hr />
          <p className="pl-3">Signed up successfully</p>
        </div>
      </div>
    </Fragment>
  );
};
