import axios from "axios";
import React, { useEffect, Fragment, useState } from "react";
import { Link } from "react-router-dom";
import ContentLoader from "react-content-loader";

export const CompaniesView = (props) => {
  const [companies, setCompanies] = useState([]);
  const [loader, setLoader] = useState(true);

  useEffect(() => {
    axios
      .get(`https://ebusiness.el-nemr.net/api/company-category/${props.slug}`)
      .then((res) => {
        setCompanies(res.data.response.data);
        setLoader(false);
      });
  }, [props.slug]);

  return (
    <Fragment>
      <div className={loader ? "d-none" : "py-4"}>
        <h1 style={{ fontWeight: "900" }}>{props.category} companies</h1>
      </div>
      <div className="row">
        <div className={loader ? "pl-5 col-md-6" : "d-none"}>
          {" "}
          <ContentLoader style={{ width: "100%" }}>
            <rect x="0" y="0" rx="5" ry="5" width="120" height="120" />
            <rect x="130" y="17" rx="4" ry="4" width="700" height="23" />
            <rect x="130" y="60" rx="3" ry="3" width="600" height="18" />
          </ContentLoader>
        </div>

        {companies.map((company, i) => {
          return (
            <div key={i} className="col-md-6 ">
              <h4 className="pb-3 font-weight-bold">{company.company_name}</h4>
              <p className="font-weight-bold">{company.company_address}</p>
              <Link
                to="/"
                className="font-weight-bold"
                style={{ cursor: "pointer" }}
              >
                {company.company_website}
              </Link>
              <p className="font-weight-bold">{company.company_phone}</p>
            </div>
          );
        })}
      </div>
    </Fragment>
  );
};
