import React, { Fragment, useEffect, useState } from "react";
import * as Scroll from "react-scroll";
import { Route, Switch } from "react-router-dom";
import { FiArrowUpCircle } from "react-icons/fi";
import { Nav } from "./Components/Nav";
import { Home } from "./Components/Home";
import { About } from "./pages/About us";
import { FreeTalks } from "./pages/Free talks";
import { Companies } from "./pages/Companies";
import { Login } from "./pages/Login";
import { ForgotPassword } from "./pages/Login/ForgotPassword";
import { Register } from "./pages/Login/Register";
import { Course } from "./Components/CourseById/Course";

export const App = () => {
  const [btn, setBtn] = useState(false);
  useEffect(() => {
    window.addEventListener("scroll", (e) => {
      if (window.pageYOffset > 400) {
        setBtn(true);
      } else {
        setBtn(false);
      }
    });
  });

  return (
    <Fragment>
      <FiArrowUpCircle
        onClick={() => {
          Scroll.animateScroll.scrollToTop();
        }}
        className={btn ? "btn-up" : "d-none"}
        style={{
          cursor: "pointer",
          fontSize: "3rem",
          position: "fixed",
          bottom: "45px",
          right: "10px",
          color: "black",
          zIndex: "99999",
        }}
      />

      <Switch>
        <Route exact path="/login" render={(props) => <Login />} />
        <Route
          exact
          path="/forgot-password"
          render={(props) => <ForgotPassword />}
        />
        <Route exact path="/register" render={(props) => <Register />} />
        <Route render={(props) => <OtherPagesContainer btn={btn} />} />
      </Switch>
    </Fragment>
  );
};

const OtherPagesContainer = (props) => {
  return (
    <Fragment>
      <Nav btn={props.btn} />

      <Route exact path="/about-us" render={(props) => <About />} />
      <Route path="/free-talks" render={(props) => <FreeTalks />} />
      <Route path="/companies" render={(props) => <Companies />} />
      <Route
        path="/courses/:categoryId/:courseId"
        render={(props) => <Course {...props} />}
      />
      <Route exact path="/lms" render={(props) => <Home />} />
    </Fragment>
  );
};
