import React, { Fragment } from "react";
import "./index.scss";
import * as AiIcons from "react-icons/ai";
import * as FaIcons from "react-icons/fa";
import { CourseCategories } from "./CourseCategories";
import { Instructors } from "./Instructors";
import { Testimonials } from "./Testimonials";
import { Contact } from "./ContactUs";
import { Link } from "react-router-dom";

export const Home = () => {
  return (
    <Fragment>
      <div className="home-page d-flex align-items-center py-5">
        <div className="container ">
          <h3 className="py-3">It's not about going digital ...</h3>
          <h1 className="py-3">
            It's about
            <br />
            The Science Behind it
          </h1>
          <p className="py-3">
            100% online learning from the best expertise and companies
          </p>
          <Link to="/register">
            <button className="text-white btn "> Register Now</button>
          </Link>
          <div className="social-icons">
            <Link to="/lms">
              <AiIcons.AiFillFacebook />
            </Link>
            <Link to="/lms">
              <FaIcons.FaInstagram />
            </Link>
            <Link to="/lms">
              <AiIcons.AiFillLinkedin />
            </Link>
            <Link to="/lms">
              <FaIcons.FaYoutube />
            </Link>
            <Link to="/lms">
              <FaIcons.FaWhatsapp />
            </Link>
          </div>
        </div>
      </div>
      <CourseCategories />
      <Instructors />
      <Testimonials />
      <Contact />
    </Fragment>
  );
};
