import React, { Fragment } from "react";
import * as AiIcons from "react-icons/ai";
import { NavLink } from "react-router-dom";
import "./login.scss";
import { LoginForm } from "./LoginForm";
export const Login = () => {
  return (
    <Fragment>
      <div className="container-fluid">
        <div className="row">
          <div
            style={{ position: "relative", height: "850px" }}
            className="col-md-7 img-container "
          >
            <div
              className="container"
              style={{
                position: "absolute",
                bottom: "150px",
              }}
            >
              <h2 style={{ fontWeight: "900", color: "hsla(0,0%,100%,0.65)" }}>
                One of the most important areas we can develop as professionals
                is{" "}
                <span className="text-white">
                  competence in accessing and sharing knowledge.
                </span>
              </h2>
            </div>
          </div>
          <div className="col-md-5">
            <NavLink to="/lms">
              <img
                alt="E-business"
                style={{
                  width: "100px",
                  height: "100px",
                  background: "white",
                }}
                src="https://ebusiness.el-nemr.net/storage/settings/March2020/LhSTxzmMs5JHAzvRqBht.png"
              />
            </NavLink>
            <NavLink to="/lms">
              <AiIcons.AiOutlineClose
                style={{
                  cursor: "pointer",
                  position: "absolute",
                  right: "15px",
                  top: "15px",
                  color: "black",
                  fontSize: "3rem",
                  zIndex: "99999",
                }}
              />
            </NavLink>
            <div className="py-5 w-75 m-auto">
              <h2 className="pt-5 pb-3 font-weight-bold">Login</h2>
              <h6 className="pb-3 font-weight-bold " style={{ color: "gray" }}>
                Enter your E-mail and password
              </h6>
              <LoginForm />
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
