import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import * as AiIcons from "react-icons/ai";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { pipe, settings } from "./Utils";
import axios from "axios";
import { Link } from "react-router-dom";
export const CourseView = (props) => {
  const [courses, setCourses] = useState([]);
  useEffect(() => {
    axios
      .get(
        `https://ebusiness.el-nemr.net/api/course-categories/${props.categoryId}`
      )
      .then((res) => setCourses(res.data.response.data));
  }, [props.categoryId]);

  return (
    <Slider {...settings}>
      {courses.map((course, i) => {
        return (
          <Link
            key={course.id}
            to={`/courses/${props.categoryId}/${course.id}`}
          >
            <div className="course-container ">
              <div className="course">
                <img
                  alt={course.image_alt}
                  className="img-fluid"
                  style={{ maxHeight: "180px", width: "300px" }}
                  src={course.course_image}
                />
                <div className="course-info">
                  <h5>{pipe(course.course_name)}</h5>
                  <p>
                    {course.start_date}
                    <span> ({course.instructor_name})</span>
                  </p>
                  <p>
                    <AiIcons.AiFillStar />
                    <AiIcons.AiFillStar />
                    <AiIcons.AiFillStar />
                    <AiIcons.AiFillStar />
                    <AiIcons.AiFillStar />
                    <span>({course.number_of_students} student)</span>
                  </p>
                  <h5>
                    {course.price_after_discount} EGP
                    <span
                      style={{
                        textDecoration: "line-through",
                        fontSize: "1rem",
                        color: "gray",
                      }}
                    >
                      {course.price} EGP
                    </span>
                  </h5>
                </div>
              </div>
            </div>
          </Link>
        );
      })}
    </Slider>
  );
};
