import React, { Fragment, useState, useEffect } from "react";
import * as FaIcons from "react-icons/fa";
import * as AiIcons from "react-icons/ai";
import "./sidebar.scss";
import { ResponsiveNav } from "./ResponsiveNav";
import { SideNavList } from "./NavLists/SideNavList";

export const Sidebar = () => {
  const [sidebar, setSidebar] = useState(false);
  useEffect(() => {
    window.addEventListener("keydown", (e) => {
      if (e.key === "Escape") {
        setSidebar(false);
      }
    });
  }, [sidebar]);

  return (
    <Fragment>
      <li className="nav-item link nav-link">
        <FaIcons.FaBars
          style={{ cursor: "pointer" }}
          onClick={(e) => {
            setSidebar(!sidebar);
            e.preventDefault();
          }}
        />
      </li>
      <div
        className={sidebar ? "popup" : "d-none"}
        onClick={(e) => {
          e.preventDefault();
          e.stopPropagation();
          setSidebar(false);
        }}
      ></div>
      <div className={sidebar ? "nav-items active" : "nav-items"}>
        <AiIcons.AiOutlineClose
          className="sidebar-toggle"
          onClick={(e) => {
            setSidebar(!sidebar);
            e.preventDefault();
          }}
        />
        <ResponsiveNav />
        <ul className="items">
          <li className="categories">Categories</li>

          <SideNavList />
        </ul>
      </div>
    </Fragment>
  );
};
