export const validateName = (term) => {
  const userNameRegex = /^[a-zA-Z]{2,10}$/;
  if (userNameRegex.test(term) === true) {
    return true;
  } else {
    return false;
  }
};
export const validateEmail = (term) => {
  const userMailRegex = /^[a-zA-Z0-9.]{2,15}(@)[a-zA-Z]{3,8}(.com)$/;
  if (userMailRegex.test(term) === true) {
    return true;
  } else {
    return false;
  }
};
export const validatePassword = (term) => {
  const userPasswordRegex = /^.{8,20}$/;
  if (userPasswordRegex.test(term) === true) {
    return true;
  } else {
    return false;
  }
};
export const validateMobile = (term) => {
  const userMobileRegex = /^(010|011|012|015)[0-9]{8}$/;
  if (userMobileRegex.test(term) === true) {
    return true;
  } else {
    return false;
  }
};

export const validateCode = (term) => {
  const codeRegex = /^.{4}$/;
  if (codeRegex.test(term) === true) {
    return true;
  } else {
    return false;
  }
};
