import React, { Fragment, useState } from "react";
import * as AiIcons from "react-icons/ai";
import { NavLink } from "react-router-dom";
import "../login.scss";
import "../Utils";
import {
  validateEmail,
  validateMobile,
  validateName,
  validatePassword,
} from "../Utils";
import { RegisterNext } from "./RegisterNext";

export const Register = () => {
  const [next, setNext] = useState(false);
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [mobile, setMobile] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [rePassword, setRePassword] = useState("");
  const [errors, setErrors] = useState({});
  const [users, setUsers] = useState([]);

  const rePassCheck = () => {
    if (rePassword !== password) {
      return false;
    } else {
      return true;
    }
  };
  // let user = {
  //   firstName: firstName,
  //   lastName: lastName,
  //   mobile: mobile,
  //   email: email,
  //   password: password,
  // };
  // setUsers([...users, user]);
  // useEffect(() => {
  //   setUsers([JSON.parse(localStorage.getItem("usersData"))]);
  // }, []);

  // useEffect(() => {
  //     // localStorage.setItem("usersData", JSON.stringify(users));
  // }, [users]);
  return (
    <Fragment>
      <div className="container-fluid">
        <div className="row">
          <div
            style={{ position: "relative" }}
            className="col-md-7 img-container "
          >
            <div
              className="container"
              style={{
                position: "absolute",
                bottom: "150px",
              }}
            >
              <h2 style={{ fontWeight: "900", color: "hsla(0,0%,100%,0.65)" }}>
                One of the most important areas we can develop as professionals
                is{" "}
                <span className="text-white">
                  competence in accessing and sharing knowledge.
                </span>
              </h2>
            </div>
          </div>
          <div className={next ? "col-md-5" : "d-none"}>
            <RegisterNext setUsers={setUsers} users={users} />
          </div>
          <div className={next ? "d-none" : "col-md-5"}>
            <NavLink to="/lms">
              <img
                alt="E-business"
                style={{
                  width: "100px",
                  height: "100px",
                  background: "white",
                }}
                src="https://ebusiness.el-nemr.net/storage/settings/March2020/LhSTxzmMs5JHAzvRqBht.png"
              />
            </NavLink>
            <NavLink to="/lms">
              <AiIcons.AiOutlineClose
                style={{
                  color: "black",
                  cursor: "pointer",
                  position: "absolute",
                  right: "15px",
                  top: "15px",
                  fontSize: "3rem",
                  zIndex: "99999",
                }}
              />
            </NavLink>
            <div className="py-5 w-75 m-auto">
              <h2 className="pt-5 pb-3 font-weight-bold">Sin Up</h2>
              <h6 className="pb-3 font-weight-bold " style={{ color: "#09c" }}>
                Enter your info to create a new account
              </h6>
              <form
                className="w-100 m-auto row justify-content-center"
                onSubmit={(e) => {
                  e.preventDefault();
                  e.stopPropagation();
                  setErrors({
                    first: !validateName(firstName),
                    last: !validateName(firstName),
                    mobile: !validateMobile(mobile),
                    email: !validateEmail(email),
                    password: !validatePassword(password),
                    rePassword: !rePassCheck(),
                  });
                  if (
                    validateName(firstName) === false ||
                    validateName(firstName) === false ||
                    validateMobile(mobile) === false ||
                    validateEmail(email) === false ||
                    validatePassword(password) === false ||
                    rePassCheck() === false
                  ) {
                    return;
                  } else {
                    setNext(!next);
                  }
                }}
              >
                <div className="col-6 pl-0">
                  <input
                    type="text"
                    placeholder="    First Name"
                    className="form-control py-3 "
                    value={firstName}
                    onChange={(e) => {
                      setFirstName(e.target.value);
                    }}
                  />
                  <small
                    className={
                      errors.first ? "text-danger font-weight-bold" : undefined
                    }
                  >
                    {errors.first ? "Not valid name" : "2-8 characters"}
                  </small>
                </div>
                <div className="col-6 pr-0 ">
                  <input
                    value={lastName}
                    onChange={(e) => {
                      setLastName(e.target.value);
                    }}
                    type="text"
                    placeholder="    Last Name"
                    className="form-control py-3 "
                  />
                  <small
                    className={
                      errors.last ? "text-danger font-weight-bold" : undefined
                    }
                  >
                    {errors.last ? "Not valid name" : "2-8 characters"}
                  </small>
                </div>
                <div className="col-12 pr-0 pl-0 py-4">
                  <input
                    value={email}
                    onChange={(e) => {
                      setEmail(e.target.value);
                    }}
                    type="email"
                    placeholder="    E-mail"
                    className="form-control py-3"
                  />{" "}
                  <small
                    className={
                      errors.email ? "text-danger font-weight-bold" : undefined
                    }
                  >
                    {errors.email
                      ? "Not valid E-mail"
                      : "Please enter a valid E-mail"}
                  </small>
                </div>
                <div className="col-12 pl-0 pr-0 ">
                  <input
                    value={mobile}
                    onChange={(e) => {
                      setMobile(e.target.value);
                    }}
                    type="phone"
                    placeholder="    Mobile Number"
                    className="form-control  py-3"
                  />{" "}
                  <small
                    className={
                      errors.mobile ? "text-danger font-weight-bold" : undefined
                    }
                  >
                    {errors.mobile
                      ? "Not valid mobile number"
                      : "Please enter valid mobile number"}
                  </small>
                </div>
                <div className="col-6 py-4 pl-0 ">
                  <input
                    value={password}
                    onChange={(e) => {
                      setPassword(e.target.value);
                    }}
                    type="password"
                    placeholder="    Enter Password"
                    className="form-control py-3"
                  />{" "}
                  <small
                    className={
                      errors.password
                        ? "text-danger font-weight-bold"
                        : undefined
                    }
                  >
                    {errors.password
                      ? "Not valid password"
                      : "Must be at least 8 characters"}
                  </small>
                </div>
                <div className="col-6 py-4 pr-0 ">
                  <input
                    value={rePassword}
                    onChange={(e) => {
                      setRePassword(e.target.value);
                    }}
                    type="password"
                    placeholder="    Re-Enter Password"
                    className="form-control  py-3"
                  />{" "}
                  <small
                    className={
                      errors.rePassword
                        ? "text-danger font-weight-bold"
                        : undefined
                    }
                  >
                    {errors.rePassword ? "Not match" : undefined}
                  </small>
                </div>
                <div className="text-center w-100 pb-3">
                  {" "}
                  <br />{" "}
                  <button
                    className="text-center w-75 py-2 btn btn-primary"
                    style={{
                      borderRadius: "50px",
                      fontSize: "1.5rem",
                      fontWeight: "800",
                    }}
                  >
                    Next
                  </button>
                  <p className="pt-4">Already have an account? </p>
                  <NavLink
                    style={{
                      background: "white",
                      color: "#09c",
                      fontSize: "1.5rem",
                      textDecoration: "none",
                      fontWeight: "900",
                    }}
                    to="/login"
                  >
                    Login
                  </NavLink>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
