import React, { useState, Fragment, useEffect } from "react";
import Slider from "react-slick";
import * as AiIcons from "react-icons/ai";
import * as FaIcons from "react-icons/fa";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { NavLink } from "react-router-dom";
import { CompaniesView } from "./CompaniesView";
import "./companies.scss";
import axios from "axios";

export const Companies = () => {
  const [slug, setSlug] = useState("business-development");
  const [category, setCategory] = useState("Business Development");
  let settings = {
    infinite: false,
    speed: 1000,
    arrows: true,
    slidesToShow: 5,
    slidesToScroll: 3,

    responsive: [
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  const [companyCategory, setCompanyCategory] = useState([]);

  useEffect(() => {
    axios
      .get("https://ebusiness.el-nemr.net/api/company-category")
      .then((res) => {
        setCompanyCategory(res.data.response.data);
      });
  }, []);
  return (
    <Fragment>
      <div className="row align-items-center" style={{ height: "600px" }}>
        <div className="container-fluid">
          <div style={{ height: "100px", width: "90%", margin: "auto" }}>
            <Slider {...settings}>
              {companyCategory.map((category, i) => (
                <div key={i} className="nav-field ">
                  <div
                    onClick={() => {
                      setSlug(category.slug);
                      setCategory(category.category_name);
                    }}
                    className={
                      slug === category.slug
                        ? "field-name text-center active"
                        : "field-name text-center"
                    }
                  >
                    <h4>{category.category_name}</h4>
                  </div>
                </div>
              ))}
            </Slider>
            <CompaniesView slug={slug} category={category} />{" "}
          </div>
        </div>
      </div>

      <div className="container footer py-3">
        <div className="row">
          <div className="col-md-4  pt-4">
            <div className="social  d-flex ">
              <NavLink exact className="media" to="/lms">
                <AiIcons.AiFillFacebook />
              </NavLink>
              <NavLink exact className="media" to="/lms">
                <FaIcons.FaInstagram />
              </NavLink>
              <NavLink exact className="media" to="/lms">
                <AiIcons.AiFillLinkedin />
              </NavLink>
              <NavLink exact className="media" to="/lms">
                <FaIcons.FaYoutube />
              </NavLink>
              <NavLink exact className="media" to="/lms">
                <FaIcons.FaWhatsapp />
              </NavLink>
            </div>
          </div>
          <div className="col-md-4 text-center pt-4">
            <p>© 2020 E-business school. All rights reserved.</p>
          </div>
          <div className="text-right col-md-4">
            <NavLink style={{ fontSize: "3rem" }} exact to="/lms">
              <FaIcons.FaTelegram />
            </NavLink>
          </div>
        </div>
      </div>
    </Fragment>
  );
};
