import React, { Fragment } from "react";
import { NavLink } from "react-router-dom";
import { Search } from "../Search/index";
import "./main.scss";

export const MainNavList = () => {
  return (
    <Fragment>
      <li className="nav-item">
        <Search />
      </li>
      <li className="nav-item">
        <NavLink
          target="_blank"
          activeClassName="selected"
          className="link nav-link"
          to={{ pathname: "http://blog.ebschoolonline.com/" }}
        >
          Our Blog
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink
          activeClassName="selected"
          className="link nav-link"
          exact
          to="/about-us"
        >
          About us
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink
          exact
          activeClassName="selected"
          className="link nav-link"
          to="/free-talks"
        >
          Free talks
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink
          exact
          activeClassName="selected"
          className="link nav-link"
          to="/companies"
        >
          Companies
        </NavLink>
      </li>
      <li className="nav-item">
        <NavLink
          exact
          activeClassName="selected"
          className="link nav-link"
          to="/login"
        >
          Login
        </NavLink>
      </li>
    </Fragment>
  );
};
