import React, { Fragment, useState } from "react";
import { NavLink } from "react-router-dom";
import "./login.scss";
import { validateEmail, validatePassword } from "./Utils";
export const LoginForm = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errors, setErrors] = useState({});
  const [noUserError, setNoUser] = useState(false);

  return (
    <Fragment>
      <form
        onSubmit={(e) => {
          e.preventDefault();
          setErrors({
            email: !validateEmail(email),
            password: !validatePassword(password),
          });
          if (
            validateEmail(email) === false ||
            validatePassword(password) === false
          ) {
            return;
          } else {
            setNoUser(true);
          }
        }}
      >
        <div className="mb-4 ">
          <input
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            type="email"
            placeholder="    E-mail"
            className="form-control py-4"
          />
          <small className="text-danger font-weight-bold">
            {errors.email && "Not valid Email"}
            {noUserError && "There is no user with such email"}
          </small>
        </div>
        <div className="mb-4 ">
          <input
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            type="password"
            placeholder="    password"
            className="form-control py-4"
          />{" "}
          <small className="text-danger font-weight-bold">
            {errors.password && "password must be at least 8 characters  "}
          </small>
        </div>

        <div className="text-center pb-3">
          <NavLink
            style={{
              background: "white",
              color: "red",
              textDecoration: "none",
              fontWeight: "800",
            }}
            to="/forgot-password"
          >
            Forgot password?
          </NavLink>
        </div>
        <input type="checkbox" />
        <label className="pl-2 font-weight-bold">Remember me</label>
        <div className="text-center py-3">
          {" "}
          <br />{" "}
          <button
            className="text-center w-75 py-2 btn btn-primary"
            style={{
              borderRadius: "50px",
              fontSize: "1.5rem",
              fontWeight: "800",
            }}
          >
            Login
          </button>
          <p className="pt-4">Don’t have an account? </p>
          <NavLink
            style={{
              background: "white",
              color: "#09c",
              fontSize: "1.5rem",
              textDecoration: "none",
              fontWeight: "900",
            }}
            to="/register"
          >
            Sign up
          </NavLink>
        </div>
      </form>
    </Fragment>
  );
};
