import React, { useState, useEffect } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./index.scss";
import axios from "axios";
import { NavLink } from "react-router-dom";
import { CourseView } from "./CourseView";
export const CourseCategories = () => {
  const [categoryId, setCategoryId] = useState(1);
  // const [courseCategory, setCourseCategory] = useState("Entrepreneurship");
  const [categories, setCategories] = useState([]);

  useEffect(() => {
    axios
      .get("https://ebusiness.el-nemr.net/api/course-categories")
      .then((res) => {
        console.log(res.data);
      });
  }, []);
  // useEffect(() => {
  //   const toLower = (term) => {
  //     let newTerm = term.toLowerCase().replace(" ", "-");
  //     return newTerm;
  //   };
  //   setCourseCategory(toLower(courseCategory));
  // }, [courseCategory]);

  return (
    <div
      className="container categories-container py-5 "
      style={{ height: "700px" }}
    >
      <h1 className="my-4">Course Categories</h1>
      <nav className="navbar navbar-expand-lg navbar-light bg-white">
        <div className="container">
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav row  mb-2 ">
              {categories.map((category) => {
                return (
                  <li className="nav-item" key={category.id}>
                    <NavLink
                      style={{
                        background: "white",
                        fontSize: "1.5rem",
                        color: "#09c",
                      }}
                      to="/"
                      className={
                        categoryId === category.id
                          ? "nav-link active-category"
                          : "nav-link"
                      }
                      onClick={() => {
                        setCategoryId(category.id);
                        // setCourseCategory(category.category_name);
                      }}
                    >
                      {category.category_name}
                    </NavLink>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
      </nav>
      <CourseView categoryId={categoryId} />
    </div>
  );
};
