import React, { Fragment, useState } from "react";
import { MainNavList } from "./NavLists/MainNavList";
import { SideNavList } from "./NavLists/SideNavList";
import { RiArrowDropDownLine } from "react-icons/ri";
export const ResponsiveNav = () => {
  const [cat, setCat] = useState(false);
  return (
    <Fragment>
      <ul className="res-items ">
        <div className={cat ? "d-none " : "side-nav-list"}>
          <MainNavList />
        </div>
        <li
          onClick={() => setCat(!cat)}
          className="categories text-dark"
          style={{
            backgroundColor: "lightcyan",
            borderRadius: "20px",
            cursor: "pointer",
          }}
        >
          Categories
          <span>
            <RiArrowDropDownLine />
          </span>
        </li>
        <div className={cat ? "" : "d-none"}>
          <SideNavList />
        </div>
      </ul>
    </Fragment>
  );
};
